* {
   --main-color: #252525;
   --main-color-light: #353535;
   --bg: linear-gradient(rgba(0, 0, 0, 0), var(--main-color)),
      url(/static/media/WarrenBuffett.6849661d.jpg) no-repeat center center;
   --bg-dark: linear-gradient(rgba(0, 0, 0, 0.25), var(--main-color)),
      url(/static/media/WarrenBuffett.6849661d.jpg) no-repeat center center;
   margin: 0;
   padding: 0;
   -webkit-touch-callout: none; /* iOS Safari */
   -webkit-user-select: none; /* Safari */ /* Konqueror HTML */ /* Old versions of Firefox */
   -ms-user-select: none; /* Internet Explorer/Edge */
   user-select: none;
}

::-webkit-scrollbar {
   width: 3px;
}
::-webkit-scrollbar-track {
   background: var(--main-color);
}
::-webkit-scrollbar-thumb {
   background-color: grey;
   border-radius: 20px;
}

.App {
   width: 100%;
   background-color: var(--main-color);
   /* font-family: "Playfair Display", serif; */
   font-family: 'Assistant', sans-serif;
   font-weight: 600;
   display: flex;
   flex-wrap: wrap;
   justify-content: center;
}

.LoadingBG {
   background-color: var(--main-color);
   width: 100%;
   height: 100vh;
}

/* Home Page TopProphet/TopPrediction Common CSS */
.TitleAndButtons {
   width: 100%;
   margin: 0 1.5vw;
   display: flex;
   justify-content: space-between;
   align-items: center;
   color: white;
}

@media screen and (max-width: 520px) {
   .TitleAndButtons {
      flex-direction: column;
      align-items: flex-start;
   }

   .TitleButtons {
      margin-top: 1vh;
   }
}

.TitleAndButtons h2 {
   color: white;
   margin: 0 0;
}

/* Material UI Button */
.MuiButton-outlined {
   border: 1.5px solid rgb(71, 71, 71) !important;
   margin: 0 5px !important;
   border-radius: 0% !important;
   color: white !important;
}

.MuiButton-root:hover {
   background: rgb(71, 71, 71) !important;
}



/* Home and Search Spin */
.ant-spin-dot-item {
   background-color: white;
}

.ant-spin-nested-loading {
   width: 100%;
}

.ant-spin-blur {
   opacity: 0 !important;
}

.ant-spin-lg .ant-spin-dot {
   margin-top: 15vh !important;
   font-size: 60px !important;
}

.ant-spin-lg .ant-spin-dot i {
   width: 30px !important;
   height: 30px !important;
}

.ant-spin-container::after {
   background: transparent !important;
}

.Navbar {
   width: 100%;
   height: 6vh;
   background: transparent;
   transition: background 0.3s ease-out;
   z-index: 1000;

   display: flex;

   list-style: none;
   position: fixed;
   font-size: 1.1rem;
   /* background-color: blue; */
}

.NavbarActive {
   background: #303030c5;
   transition: background 0.3s ease-in;
}

.Navbar ul {
   height: 100%;
   list-style: none;
   display: inline-block;
   display: flex;
   align-items: stretch;
   /* border: 3px solid green; */
}

.Navbar li {
   /* border: 3px solid red; */
   height: 100%;
   display: flex;
   align-items: center;
   text-decoration: none;
   color: white;
   padding: 0 2vw;
   transition: background-color 0.5s ease-out, color 0.3s ease-out;
}

.Navbar li:hover {
   background-color: white;
   color: black;
   transition: background-color 0.5s ease-in, color 0.3s ease-in;
}

.NavLeft {
   /* border: 3px solid red; */
   width: 33%;
}

.SearchBarContainer {
   width: 34%;
   display: flex;
   justify-content: center;
   align-items: center;
}

.SearchBar {
   margin-left: 3%;
   width: 80%;
   max-width: 500px;
   height: 60%;
   align-self: center;
   border: 1px solid white;
   transition: background 0.5s ease-out;
   /* border: 3px solid red; */
   display: flex;
   align-items: center;
}

.SearchBar #SearchInput {
   /* border: 3px solid green !important; */
   width: 85%;
   height: 60%;
   background-color: transparent;
   color: white;
   border: none;
   /* transition: box-shadow 0.6s linear; */
}

.SearchBar #SearchInput:focus {
   /* box-shadow: 0 2px 2px -2px white;
   transition: box-shadow 0.2s linear; */
   box-shadow: none;
}

.SearchBar .Icon {
   width: 15%;
   height: 100%;
   display: flex;
   justify-content: center;
   align-items: center;
   color: white;
   transition: transform 0.2s ease-out;
}
.SearchBar .Icon:hover {
   transform: scale(1.2);
   transition: transform 0.2s ease-in;
}

.NavRight {
   width: 33%;
   display: flex;
   justify-content: flex-end;
}




/* Responsive NavBar Burger */
@media screen and (max-width: 768px) {
   .Navbar {
      justify-content: space-between;
   }

   .SearchBarContainer {
      width: 60%;
   }

   .Navbar ul {
      height: 5%;
      width: 60%;
   }
   .Navbar li {
      font-size: 1rem;
      width: 100%;
      border: 2px solid white;
      border-radius: 1px;
      margin-bottom: 2vh;
   }
}

.Burger {
   width: 15%;
}

/* Position and sizing of burger button */
.bm-burger-button {
   position: fixed;
   width: 6%;
   height: 2.5%;
   left: 3%;
   top: 2%;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
   background: white;
   border-radius: 5px;
}

/* Color/shape of burger icon bars on hover*/
.bm-burger-bars-hover {
   background: white;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
   height: 32px !important;
   width: 25px !important;
}

/* Color/shape of close button cross */
.bm-cross {
   background: white;
   height: 20px !important;
   width: 3px !important;
}

/*
Sidebar wrapper styles
Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
*/
.bm-menu-wrap {
   position: fixed;
   height: 100%;
}

/* General sidebar styles */
.bm-menu {
   background: var(--main-color-light);
   padding: 2.5em 1.5em 0;
   font-size: 1.15em;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
   fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
   padding: 0.8em;
}

/* Individual item */
.bm-item {
   display: inline-block;
   height: 10px;
   outline: none;
}

/* Styling of overlay */
.bm-overlay {
   background: rgba(0, 0, 0, 0.3);
}

.Cover {
